.services {
    padding-bottom: 10rem;
    margin-top: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 3px solid var(--blue-general-text);
}

.services .services__content {
    display: flex;
    flex-direction: column;
    margin-top: 3rem;
}

.services .services__content > div {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-bottom: 4rem;
}

.services .services__content .services__subtitle {
    color: rgba(195, 219, 61, 0.75);
    text-shadow: 0px 7px 7px 0px rgba(0, 0, 0, 0.25);
    font-family: Kumbh Sans;
    font-weight: 700;
    margin-bottom: 4rem;
}

.services .services__content__header {
    display: flex;
    gap: 2rem;
    align-items: center;
}

.services .services__content__header img {
    max-width: 5rem;
    height: 5.5rem;
}

.services .services__content__header h3 {
    font-size: 3.4rem;
    color: var(--yellow-card-background);
    font-weight: 700;
    line-height: normal;
}

.services ul.services__content__items {
    margin-left: 2.5rem;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
    max-width: 800px;
}

.services ul.services__content__items li {
    display: flex;
    align-items: center;
    gap: 1.5rem;
}

.services ul.services__content__items li img {
    max-width: 2.5rem;
}

.services ul.services__content__items li p {
    color: var(--gray-services-text);
    font-size: 1.8rem;
    font-family: Poppins;
    font-weight: 500;
    line-height: 169.023%;
}

.services .services__content__images {
    display: grid;
    grid-template-columns: repeat(3, 21.8rem);
    border: 3px solid var(--yellow-card-background);
    border-radius: 2rem;
    padding: 2rem 2rem;
    gap: 2.5rem;
    max-width: min-content;
    margin-left: 2rem;
}

.services .services__content__images img {
    object-fit: contain;
    height: auto;
    max-width: 21.8rem;
    border-radius: 2rem;
}





@media screen and (max-width: 1268px) {

    .services .services__content__header img {
        max-width: 4rem;
        height: 4.4rem;
    }
    
    .services .services__content__header h3 {
        font-size: 3rem;
        color: var(--yellow-card-background);
        font-weight: 700;
        line-height: normal;
    }

    .services .services__content__images {
        grid-template-columns: repeat(3, 18rem);
        gap: 2rem;
    }

    .services .services__content__images img {
        max-width: 18rem;
    }
}

@media screen and (max-width: 940px) {

    .services .services__content__header img {
        max-width: 3.6rem;
        height: 4.4rem;
    }
    
    .services .services__content__header h3 {
        font-size: 2.8rem;
    }

    .services .services__content__images {
        grid-template-columns: repeat(3, 16rem);
        gap: 2rem;
    }

    .services ul.services__content__items li img {
        max-width: 2.4rem;
    }

    .services ul.services__content__items li p {
        font-size: 1.6rem;
    }

    .services .services__content__images img {
        max-width: 16rem;
    }
}

@media screen and (max-width: 740px) {
    .services .services__content__header {
        gap: 1.5rem;
    }

    .services .services__content__header > img {
        max-width: 2.8rem;
        height: 3rem;
    }
    
    .services .services__content__header h3 {
        font-size: 2.2rem;
    }

    .services ul.services__content__items li p {
        font-size: 1.5rem;
    }

    .services ul.services__content__items li img {
        max-width: 2.2rem;
    }

    .services .services__content__images {
        grid-template-columns: repeat(3, 13rem);
        gap: 1.5rem;
    }

    .services .services__content__images img {
        max-width: 13rem;
    }
}

@media screen and (max-width: 740px) {

    .services .services__content {
        padding-left: 2rem;
    }
    
    .services .services__content__header {
        gap: 1.5rem;
    }

    .services .services__content__header > img {
        max-width: 2.4rem;
        height: 3rem;
    }
    
    .services .services__content__header h3 {
        font-size: 2.2rem;
    }

    .services ul.services__content__items li p {
        font-size: 1.4rem;
    }

    .services ul.services__content__items li img {
        max-width: 2.2rem;
    }

    .services .services__content__images {
        grid-template-columns: repeat(3, 11rem);
        gap: 1.5rem;
    }

    .services .services__content__images img {
        max-width: 11rem;
    }
}

@media screen and (max-width: 430px) {

    .services .services__content {
        padding-left: 2rem;
    }
    .services .services__content__header {
        gap: 1.5rem;
    }

    .services .services__content__header > img {
        max-width: 2.2rem;
    }
    
    .services .services__content__header h3 {
        font-size: 2.1rem;
    }

    .services ul.services__content__items {
        flex-wrap: wrap;
    }

    .services ul.services__content__items li {
        gap: 1rem;
    }
    .services ul.services__content__items li p {
        font-size: 1.2rem;
    }

    .services ul.services__content__items li img {
        max-width: 1.6rem;
    }

    .services .services__content__images {
        padding: 1rem;
        grid-template-columns: repeat(3, 9rem);
        gap: 1rem;
    }

    .services .services__content__images img {
        max-width: 9rem;
    }
}







